<template>
	<view class="box">
		<view class="set-li"  v-if="type_check">
			<view class="title">
				个人资料设置
			</view>
			<view class="iconfont iconxiayibu">
			</view>
		</view>
		<navigator url="/pages/user/change_password" class="set-li"  v-if="type_check">
			<view class="title">
				修改账户密码
			</view>
			<view class="iconfont iconxiayibu">
			</view>
		</navigator>
		<view class="set-li"  v-if="type_check">
			<view class="title">
				关于平台
			</view>
			<view class="iconfont iconxiayibu">
			</view>
		</view>
		<view class="blank"></view>
		<button class="no-btn confirm-btn" @tap="logout">退出登录</button>
	</view>
</template>

<script>
	import {
		mapState
	} from 'vuex';
	export default {
		data() {
			return {}
		},
		onLoad() {

		},
		computed: {
			...mapState({
				type_check: state => state.type_check,
			})
		
		},
		methods: {
			logout() {
				this.$common.removeLocal('token')
				this.$common.removeLocal('customerSeqId')
				this.$common.removeLocal('customerName')
				this.$store.commit('saveUser', '')
				this.$store.commit('saveOrderConfirm', {})
				this.$store.commit('saveShop', {})
				this.$store.commit('saveAddress', {})
				this.$store.commit('saveAccount', {})
				uni.setStorageSync('setStatusIndexFunc', 0)
				uni.navigateTo({
					url:'/pages/login/login'
				})
			},
		}
	}
</script>

<style lang="scss">
	.confirm-btn{
		width: calc(100% - 60rpx);
	    background: #ffa098;
	    margin: 30px auto;
	    height: 40px;
	    line-height: 40px;
	    border-radius: 50px;
	    font-size: 30rpx;
	    padding: 0;
	    font-weight: bold;
	    color: white;
	    text-align: center;
	    cursor: pointer;
	}
	.box {
		width: 100%;
		min-height: calc(100vh - var(--window-top));
		background: white;

		.set-li {
			display: flex;
			justify-content: space-between;
			align-items: center;
			width: calc(100% - 68rpx);
			padding: 0 34rpx;
			height: 88rpx;
			font-size: 30rpx;

		}
	}

	.logout {
		width: 682rpx;
		margin: 0 auto;
		margin-top: 180rpx;
	}
</style>
